export const initialState = {
  findQueries: false, // false, true or Date.now()
  worksheetId: false,
  message: undefined,
  queryUpdateQueue: [],
  queries: [],
};

export const reducer = (state, action) => {
  switch (action.type) {
    case 'RESET':
      return initialState;
    case 'SET_MESSAGE':
      return {
        ...initialState,
        message: action.message,
      };
    case 'START':
      console.log('START');
      return {
        ...initialState,
        findQueries: true,
      };
    case 'INIT':
      console.log('INIT');
      return {
        findQueries: Date.now(),
        worksheetId: action.worksheetId,
        queryUpdateQueue: [],
        queries: [
          ...state.queries,
          {
            anchorAddress: action.anchorAddress,
            tsCodesAddress: action.tsCodesAddress, // Sheet1!C1:D1
            tsCodes: action.tsCodes, // ['ABS-ABSAHPI', 'NMS-TOT_PAS']
            warning: action.warning,
            frequency: 'M',
            step1: 'DONE', // Find anchor:   DONE
            step2: 'BUSY', // Isolate codes: TODO, BUSY, DONE
            step3: 'TODO', // Fetch data:    TODO, BUSY, DONE, ERROR
            step4: 'TODO', // Store data:    TODO, BUSY, DONE, ERROR
            step5: 'TODO', // Update data:   TODO, BUSY, DONE, ERROR
          },
        ],
      };
    case 'INSERT_DATES_AND_META': {
      const idx = state.queries
        .findIndex((obj) => obj.anchorAddress === action.anchorAddress);
      return {
        ...state,
        queries: [
          ...state.queries.slice(0, idx),
          {
            ...state.queries[idx],
            startYear: action.startYear, // 1980
            newStartYear: action.startYear, // 1980
            dataRowCount: action.dataRowCount, // 100
            metaAddress: action.metaAddress, // Sheet1!B2:C7
            meta: action.meta, // {}
            frequency: action.frequency,
            newFrequency: action.frequency,
            selectionTitle: action.selectionTitle,
            lastUpdated: action.lastUpdated,
            step2: 'DONE',
          },
          ...state.queries.slice(idx + 1),
        ],
      };
    }
    case 'INSERT_WARNING': {
      const idx = state.queries
        .findIndex((obj) => obj.anchorAddress === action.anchorAddress);
      return {
        ...state,
        queries: [
          ...state.queries.slice(0, idx),
          {
            ...state.queries[idx],
            warning: action.warning,
            step2: 'DONE',
            step3: 'DONE',
            step4: 'DONE',
            step5: 'DONE',
          },
          ...state.queries.slice(idx + 1),
        ],
      };
    }
    case 'EDIT_FREQUENCY': {
      const idx = state.queries
        .findIndex((obj) => obj.anchorAddress === action.anchorAddress);
      return {
        ...state,
        queries: [
          ...state.queries.slice(0, idx),
          {
            ...state.queries[idx],
            newFrequency: action.newFrequency,
          },
          ...state.queries.slice(idx + 1),
        ],
      };
    }
    case 'EDIT_STARTYEAR': {
      const idx = state.queries
        .findIndex((obj) => obj.anchorAddress === action.anchorAddress);
      return {
        ...state,
        queries: [
          ...state.queries.slice(0, idx),
          {
            ...state.queries[idx],
            newStartYear: action.newStartYear,
          },
          ...state.queries.slice(idx + 1),
        ],
      };
    }
    case 'EDIT_LASTUPDATED': {
      const idx = state.queries
        .findIndex((obj) => obj.anchorAddress === action.anchorAddress);
      return {
        ...state,
        queries: [
          ...state.queries.slice(0, idx),
          {
            ...state.queries[idx],
            lastUpdated: action.newLastUpdated,
          },
          ...state.queries.slice(idx + 1),
        ],
      };
    }
    case 'FETCH_DATA': {
      const idx = state.queries
        .findIndex((obj) => obj.anchorAddress === action.anchorAddress);
      return {
        ...state,
        queries: [
          ...state.queries.slice(0, idx),
          {
            ...state.queries[idx],
            step3: action.status,
            step4: 'TODO',
            step5: 'TODO',
          },
          ...state.queries.slice(idx + 1),
        ],
      };
    }
    case 'SKIP_FETCH_DATA': {
      const idx = state.queries
        .findIndex((obj) => obj.anchorAddress === action.anchorAddress);
      return {
        ...state,
        queries: [
          ...state.queries.slice(0, idx),
          {
            ...state.queries[idx],
            step3: 'DONE',
            step4: 'DONE',
            step5: 'DONE',
          },
          ...state.queries.slice(idx + 1),
        ],
      };
    }
    case 'INSERT_DATA': {
      const idx = state.queries
        .findIndex((obj) => obj.anchorAddress === action.anchorAddress);

      const dataRowCount = action.data[state.queries[idx].newFrequency].ts_li[0].values.length;
      console.log(action.data); // dataRowCount);
      return {
        ...state,
        queryUpdateQueue: [
          ...state.queryUpdateQueue,
          action.anchorAddress,
        ],
        queries: [
          ...state.queries.slice(0, idx),
          {
            ...state.queries[idx],
            data: action.data,
            startYear: state.queries[idx].newStartYear,
            frequency: state.queries[idx].newFrequency,
            dataRowCount,
            step3: 'DONE',
            step4: 'DONE',
          },
          ...state.queries.slice(idx + 1),
        ],
      };
    }
    case 'UPDATE_DATA': {
      const idx = state.queries
        .findIndex((obj) => obj.anchorAddress === action.anchorAddress);
      return {
        ...state,
        queries: [
          ...state.queries.slice(0, idx),
          {
            ...state.queries[idx],
            step5: action.status,
          },
          ...state.queries.slice(idx + 1),
        ],
      };
    }
    case 'REMOVE_FROMQUEUE':
      return {
        ...state,
        queryUpdateQueue: state.queryUpdateQueue.slice(1),
      };
    default:
      throw new Error();
  }
};
